import React, { useState, useEffect } from 'react';
import { Button, Divider, Form, Tabs, Row, Col, Input, InputNumber, Checkbox, message } from 'antd';
import { parseLanguage } from '../../../helpers/LanguageHelper';
import { Editor } from 'react-draft-wysiwyg';
import { EditorState, ContentState, convertToRaw } from 'draft-js';
import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import draftToHtml from 'draftjs-to-html';
import htmlToDraft from 'html-to-draftjs';
import { updateRoomType, getRoomTypeById } from '../../../api/RoomTypeApi';

const FormItem = Form.Item;
const TabPane = Tabs.TabPane;

function EditRoomTypeInfoForm(props) {
  const [roomType, setRoomType] = useState([]);
	const [description, setDescription] = useState({});
	const [loading, setLoading] = useState(false);

	useEffect(() => {
    if (props.match.params.itemId) {
			fetchRoomTypes(props.match.params.itemId);
    }
  }, [props.match.params.itemId])
  
  const fetchRoomTypes = async (id) => {
    getRoomTypeById({ roomId: id }).then(res => {
      if (res.status === 'SUCCESS' && res.value) {
				setRoomType(res.value);
				const descriptionObj = {};
				res.value.i18ns && res.value.i18ns.map(item => {
					if (item.description) {
						descriptionObj[item.lang] = EditorState.createWithContent(ContentState.createFromBlockArray(htmlToDraft(item.description).contentBlocks));
					} else {
						descriptionObj[item.lang] = EditorState.createEmpty();
					}
				})
				setDescription(descriptionObj);
			}
    })
  }

	const onEditorDescriptionChange = (value, lang) => {
    const descriptionObj = {...description};
    descriptionObj[lang] = value;
    setDescription(descriptionObj);
  }

	const handleSubmit = (e) => {
		e.preventDefault();
		if (!props.hotelId) return;
		props.form.validateFieldsAndScroll((err, values) => {
			if (!err) {
				console.log('Received values of form: ', values);
				const { title, view, maxAdults, maxGuests, roomQuantity, size, typeCode } = values;
				const i18ns = [];
				Object.entries(description).map(([key, value]) => {
					i18ns.push({
						description: draftToHtml(convertToRaw(description[key].getCurrentContent())),
						lang: key,
						title: title[key] || '',
						view: view[key] || ''
					})
				})
				const data = {
					hotelId: props.hotelId,
					roomId: parseInt(props.match.params.itemId),
					i18ns: i18ns, 
					maxAdults,
					maxGuests,
					roomQuantity,
					size,
					typeCode,
					imageId: roomType.image ? roomType.image.imageId : undefined
				}
				setLoading(true);
				updateRoomType(data).then(res => {
					setLoading(false);
					if (res.status === 'SUCCESS') {
						message.success('Cập nhật thông tin phòng thành công');
					} else {
						message.error('Cập nhật thông tin không phòng thành công');
					}
				}).catch(err => {
					setLoading(false);
					message.error(err.message);
				})
			}
		});
	}

	const formItemLayout = {
		labelCol: {
			xs: { span: 24 },
			sm: { span: 6 },
		},
		wrapperCol: {
			xs: { span: 24 },
			sm: { span: 12 },
		},
	};

	const formItemLayout2 = {
		labelCol: {
			xs: { span: 24 },
			sm: { span: 3 },
		},
		wrapperCol: {
			xs: { span: 24 },
			sm: { span: 21 },
		},
	};

	const { getFieldDecorator } = props.form;

	return (
		<div>
			<Form onSubmit={handleSubmit}>
				<Divider orientation="left">Thông tin phòng: {roomType.i18ns ? roomType.i18ns[0].title : null}</Divider>
				<Tabs defaultActiveKey="0">
					{
						roomType.i18ns && roomType.i18ns.map((item, index) => {
							return (
								<TabPane tab={parseLanguage(item.lang)} key={index}>
									<Row>
										<Col xs={24}>
											<FormItem
												{...formItemLayout2}
												label="Tên loại phòng"
											>
												{getFieldDecorator(`title[${item.lang}]`, {
													initialValue: item.title,
													rules: [{
														required: true, message: 'Vui lòng nhập Tên loại phòng!'
													}],
												})(
													<Input />
												)}
											</FormItem>
										</Col>
										<Col xs={24}>
											<FormItem
												{...formItemLayout2}
												label="Hướng phòng"
											>
												{getFieldDecorator(`view[${item.lang}]`, {
													initialValue: item.view,
													rules: [{
														required: true, message: 'Vui lòng nhập Hướng phòng!'
													}],
												})(
													<Input />
												)}
											</FormItem>
										</Col>
										<Col xs={24}>
											<FormItem
												{...formItemLayout2}
												label="Thông tin mô tả"
											>
												<Editor
													editorState={description[item.lang]}
                          toolbarClassName="toolbarClassName"
                          wrapperClassName="wrapperClassName wrapperClassName-fix"
                          editorClassName="editorClassName editorClassName-fix"
                          onEditorStateChange={(value) => onEditorDescriptionChange(value, item.lang)}
												/>
											</FormItem>
										</Col>
									</Row>
								</TabPane>
							)
						})
					}
				</Tabs>
				<Divider />

				<Row gutter={16}>
					<Col xs={24} sm={12}>
						<FormItem
							{...formItemLayout}
							label="Số khách tối đa"
						>
							{getFieldDecorator('maxGuests', {
                initialValue: roomType.maxGuests,
								rules: [{
									required: true, message: 'Vui lòng nhập Số khách tối đa!'
								}],
							})(
								<InputNumber style={{ width: '100%' }} />
							)}
						</FormItem>
					</Col>

					<Col xs={24} sm={12}>
						<FormItem
							{...formItemLayout}
							label="Giá ngày thường"
						>
							{getFieldDecorator('weekdayPrice', {
                initialValue: roomType.weekdayPrice,
								rules: [{
									required: true, message: 'Vui lòng nhập Giá ngày thường!'
								}],
							})(
								<InputNumber style={{ width: '100%' }} />
							)}
						</FormItem>
					</Col>

					<Col xs={24} sm={12}>
						<FormItem
							{...formItemLayout}
							label="Số người lớn tối đa"
						>
							{getFieldDecorator('maxAdults', {
                initialValue: roomType.maxAdults,
								rules: [{
									required: true, message: 'Vui lòng nhập Số người lớn tối đa!'
								}],
							})(
								<InputNumber style={{ width: '100%' }} />
							)}
						</FormItem>
					</Col>

					<Col xs={24} sm={12}>
						<FormItem
							{...formItemLayout}
							label="Giá cuối tuần"
						>
							{getFieldDecorator('weekendPrice', {
                initialValue: roomType.weekendPrice,
								rules: [{
									required: true, message: 'Vui lòng nhập Giá cuối tuần!'
								}],
							})(
								<InputNumber style={{ width: '100%' }} />
							)}
						</FormItem>
					</Col>

					<Col xs={24} sm={12}>
						<FormItem
							{...formItemLayout}
							label="Tổng số phòng"
						>
							{getFieldDecorator('roomQuantity', {
                initialValue: roomType.roomQuantity,
								rules: [{
									required: true, message: 'Vui lòng nhập Tổng số phòng!'
								}],
							})(
								<InputNumber style={{ width: '100%' }} />
							)}
						</FormItem>
					</Col>

					<Col xs={24} sm={12}>
						<FormItem
							{...formItemLayout}
							label="Kích thước phòng (m2)"
						>
							{getFieldDecorator('size', {
                initialValue: roomType.size,
								rules: [{
									required: true, message: 'Vui lòng nhập Kích thước phòng!'
								}],
							})(
								<InputNumber style={{ width: '100%' }} />
							)}
						</FormItem>
					</Col>

					<Col xs={24} sm={12}>
						<FormItem
							{...formItemLayout}
							label="Số phòng trống mặc định"
						>
							{getFieldDecorator('emptyRoom', {
                initialValue: roomType.emptyRoom,
								rules: [{
									required: true, message: 'Vui lòng nhập Số phòng trống mặc định!'
								}],
							})(
								<InputNumber style={{ width: '100%' }} />
							)}
						</FormItem>
					</Col>

					<Col xs={24} sm={12}>
						<FormItem
							{...formItemLayout}
							label="Mã loại phòng"
						>
							{getFieldDecorator('typeCode', {
                initialValue: roomType.typeCode,
								rules: [{
									required: true, message: 'Vui lòng nhập Mã loại phòng!'
								}],
							})(
								<Input />
							)}
						</FormItem>
					</Col>
					<Col xs={24} sm={12}>
						<FormItem>
							{getFieldDecorator('remember', {
								valuePropName: 'checked',
							})(
								<Checkbox>Không cho phép trẻ em</Checkbox>
							)}
						</FormItem>
					</Col>
				</Row>
				<Button type='primary' htmlType='submit' loading={loading}>
					Lưu
				</Button>
			</Form>
		</div>
	)
}

const EditRoomTypeInfo = Form.create()(EditRoomTypeInfoForm);
export default EditRoomTypeInfo;