import React, { useEffect } from 'react';
import { Tabs, Button } from 'antd';
import EditRoomTypeInfo from './EditRoomTypeTab/EditRoomTypeInfo';
import EditRoomTypeAmenities from './EditRoomTypeTab/EditRoomTypeAmenities';
import EditRoomTypeImages from './EditRoomTypeTab/EditRoomTypeImages';
const TabPane = Tabs.TabPane;

const routes = [
  {
      title: 'Thông tin phòng',
      component: EditRoomTypeInfo
  },
  {
      title: 'Tiện nghi phòng',
      component: EditRoomTypeAmenities
  },
  {
      title: 'Hình ảnh phòng',
      component: EditRoomTypeImages
  }
]

export default function EditRoomType(props) {

  useEffect(() => {
    console.log(props);
  }, [])

  return (
    <div>
      <Button onClick={() => props.history.goBack()} icon='left'>
				Quay lại
			</Button>
      <Tabs defaultActiveKey='0'>
        {
					routes.map((child, index) => {
						return (
							<TabPane tab={child.title} key={index}>
								<child.component {...props} />
							</TabPane>
						)
					})
        }
      </Tabs>
    </div>
  )
}
